.wordle-container {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-direction: column;
  flex-grow: 1;
  overflow: hidden;
  width: 100%;
  padding: 0;
}

.wordle-row {
  display: flex;
  align-content: center;
  justify-content: center;
}

.wordle-tileContainer {
  --tile-dim: 62px;
  width: var(--tile-dim);
  height: var(--tile-dim);
  margin: 5px;
  display: flex;
}

.wordle-tile {
  width: 100%;
  height: 100%;
  background-color: var(--square-color);
  color: var(--letter-color);
  margin: auto;
}

@media only screen and (max-width: 969px) {
  .wordle-tileContainer {
    --tile-dim: 48px;
  }
}

.value {
  margin: auto;
}

.hole {
  opacity: 0;
}

.correct {
  background-color: var(--correct-letter);
}

.misplaced {
  background-color: var(--misplaced-letter);
}

.guessed {
  animation: flip 1s ease-in-out;
}

.column1 {
  animation-delay: 150ms;
}

.column2 {
  animation-delay: 300ms;
}

.column3 {
  animation-delay: 450ms;
}

.column4 {
  animation-delay: 600ms;
}

@keyframes flip {
  0%   { height: var(--tile-dim); background-color: var(--square-color); }
  50%  { height: 0; background-color: var(--square-color); }
  100% { height: var(--tile-dim) }
}

button {
  margin: auto;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 20px;
  border-radius: 5px;    
}

kbd {
  background-color: #eee;
  border-radius: 3px;
  border: 1px solid #b4b4b4;
  box-shadow: 0 1px 1px rgba(0, 0, 0, .2), 0 2px 0 0 rgba(255, 255, 255, .7) inset;
  color: #333;
  display: inline-block;
  font-size: .85em;
  font-weight: 700;
  line-height: 1;
  padding: 2px 4px;
  white-space: nowrap;
}